/* ROBOT.GIF from Giphy - "href="https://giphy.com/gifs/animation-3d-3oxHQk2aTC2bCYxoRi"*/

html {
  box-sizing: border-box;
}

body {
  margin: 0;
  background: #394452;
}

.container {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url("./robot.gif");
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
}

button {
  cursor: pointer;
  outline: none;
  width: 200px;
  height: 50px;
  font-family: "Courier New", Courier, monospace;
  font-size: 20px;
  color: white;
  background: #495159;
  border: none;
  border-radius: 5px;
  box-shadow: 2px 2px 20px 10px rgba(0, 0, 0, 0.2);
  position: relative;
  top: 30%;
}

button:hover {
  filter: brightness(95%);
}

button:active {
  transform: scale(0.98);
}

button:disabled {
  cursor: default;
  filter: brightness(30%);
}

/* Media Query Here */
@media screen and (max-width: 1000px) {
  .container {
    background-size: cover;
    background-position: center center;
  }

  button {
    box-shadow: 5p 5px 30px 20px rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 81%;
  }
}
